	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked.co.uk","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/link-experiments-with-canvas-and-the-hype-framework/index.md absPath of file >>> MarkdownRemark","html":"<p>Over at <a href=\"http://markstar.co.uk\" title=\"Open markstar.co.uk in a new window.\">markstar.co.uk</a>, you can see some cool experiments that Mark has been working on, and frankly the source code behind them is very cool.</p>\n<p><img src=\"/content/images/2011/03/canvas.png\" alt=\"Look! Canvas!\" title=\"Look! Canvas!\"></p>\n<p>The experiments themselves are based on the HTML5 Canvas element, and its drawing tools.  The experiments are part of a series, exploring the initial Canvas element, and then quickly moving into some more familiar Flash-style drawing features, before exploring the possibility of converting the Flash <a href=\"http://www.hypeframework.org/\" title=\"the HYPE Framework\">HYPE Framework</a> into an HTML5 Canvas version. All pretty interesting - can't wait to see if he gets onto some of the really cool complex examples of the HYPE Framework.</p>\n<p>You can check out <a href=\"http://markstar.co.uk/blog/category/html5/canvas/\" title=\"The Canvas tagged articles archive.\">the series of experiments in the Canvas tag archive here.</a></p>","frontmatter":{"title":"Link: Experiments with Canvas and the HYPE Framework","date":"March 16, 2011","tags":["html5","canvas","markstar"]}}},"pathContext":{}}

